import React, { Component } from 'react';
import { Segment, Grid, Container, Image, Header } from 'semantic-ui-react';

import AOS from 'aos';

import 'aos/dist/aos.css';

class PageAreaExplanation extends Component
{
    static defaultProps = {
        direction: "left"
    }

    componentDidMount()
    {
        AOS.init();
    }

    render()
    {
        const { direction, text, header } = this.props;

        return(
            <Segment
                style={{ paddingTop: "5em", paddingBottom: "5em" }}
                basic
            >
                <Container 
                    as={Grid} 
                    columns={2}
                    text
                >
                    {
                        direction === "left" && (
                            <Grid.Row>
                                <Grid.Column>
                                    <Header as='h1' textAlign="left">
                                        {header}
                                    </Header>
                                    <p style={{textAlign: "left"}}>
                                        {text}
                                    </p>
                                </Grid.Column>
                                <Grid.Column>
                                    <Image
                                        style={{ marginTop: "3.5em" }}
                                        src='https://cdn.assetory.net/images/test-icon.png' 
                                        data-aos-easing="ease-in-sine"
                                        data-aos='fade-left'
                                        data-aos-duration={1000}
                                        centered 
                                        size='small'
                                    />
                                </Grid.Column>
                            </Grid.Row>
                        )
                    }
                    {                   
                        direction === "right" && (
                            <Grid.Row>
                                <Grid.Column>
                                    <Image 
                                        style={{ marginTop: "3.5em" }}
                                        src='https://cdn.assetory.net/images/test-icon.png' 
                                        data-aos-easing="ease-in-sine"
                                        data-aos='fade-right'
                                        data-aos-duration={1000}
                                        centered 
                                        size='small'
                                    />
                                </Grid.Column>
                                <Grid.Column>
                                    <Header as='h1' textAlign="right">
                                        {header}
                                    </Header>
                                    <p style={{textAlign: "right"}}>
                                        {text}
                                    </p>
                                </Grid.Column>
                            </Grid.Row>
                        )
                    }
                </Container>
            </Segment>
        )
    }
}

export default PageAreaExplanation;
