import React, { Component } from 'react';
import { Segment, Menu, Container } from 'semantic-ui-react';

import AOS from 'aos';

import 'aos/dist/aos.css';

class PageMenu extends Component
{
    componentDidMount()
    {
        AOS.init();
    }

    render()
    {
        return (
            <Segment 
                className="page-header-menu"
                basic 
                inverted 
            >
                <Menu
                    as={Container}
                    secondary 
                    inverted
                >
                    <Menu.Menu position='right'
                    
                    data-aos-duration={1000}
                    data-aos='fade-up'>
                        <Menu.Item
                            as="a"
                            href="#"
                            name='Link #1'
                        />
                        <Menu.Item
                            as="a"
                            href="#"
                            name='Link #2'
                        />
                    </Menu.Menu>
                </Menu>
            </Segment>
        )
    }
}

export default PageMenu;
