import React, { Component } from 'react';
import { Segment, Container, Image, Divider, List, Icon } from 'semantic-ui-react';

import AOS from 'aos';

import 'aos/dist/aos.css';

class PageFooter extends Component
{
    componentDidMount()
    {
        AOS.init();
    }

    render()
    {
        return (
            <Segment inverted vertical
                data-aos='fade-up'
                data-aos-duration={1000}
                style={{ minHeight: 400 }}
            >
                <Container textAlign="center" text>
                    
                    <br/>

                    <Image
                        src="https://cdn.assetory.net/images/assetory-icon-light-512.png"
                        centered
                        size="small"
                        data-aos-easing="ease-in-sine"
                        data-aos='zoom-in'
                        data-aos-duration={1000}
                    />

                    <br/>

                    <Divider horizontal>
                        <a href="https:///www.assetory.net">assetory.net</a>
                    </Divider>

                    <br/>

                    <List link inverted horizontal size="small">
                        <List.Item as='a'>
                            <Icon name="facebook f" size="big"/>
                        </List.Item>
                        <List.Item as='a'>
                            <Icon name="twitter" size="big"/>
                        </List.Item>
                        <List.Item as='a'>
                            <Icon name="instagram" size="big"/>
                        </List.Item>
                        <List.Item as='a'>
                            <Icon name="youtube" size="big"/>
                        </List.Item>
                    </List>

                    <br/>
                    <br/>

                    <List inverted size="small" divided link horizontal>
                        <List.Item as='a'>
                            Link #1
                        </List.Item>
                        <List.Item as='a'>
                            Link #2
                        </List.Item>
                        <List.Item as='a'>
                            Link #3
                        </List.Item>
                        <List.Item as='a'>
                            Link #4
                        </List.Item>
                    </List>

                    <p>Â© 2020 Assetory. All rights reserved.</p>

                </Container>
            </Segment>
        )
    }
}

export default PageFooter;
